*{
  margin: 0;
  padding: 0;
}
main{
  width:100%;
  height: 100vh;
  background: linear-gradient(to top,rgba(0,0,0,0.5)50%,rgba(0,0,0,0.5)50%), url("./images/bg_img.jpg") ;
  background-position: center;
  background-size: cover;
}

.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(210, 204, 204);
  color: white;
}
.header_container .navbar img {
  height: 30px;
  width: 30px;
  margin: 10px;
}
.logo_siteName {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;

}
.navLinks {
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 30px;
  margin-right: 20px;
}
.activeLink
 {
  text-decoration: none;
  color: #0653ec;
}
.inActiveLink
 {
  text-decoration: none;
  color: #5a3232;
}

.search_head{
width: 330px;
margin-left: 200px;
}
.inp_srch{
  font-family: 'Times New Roman';
  width: 200px;
  height: 30px;
  background: transparent;
  border: 1px solid #ff5100;
  color:rgb(30, 39, 158);
  font-size: 18px;
  padding-left: 10px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
}
.btn_srch{
  width: 100px;
  height: 30px;
  background-color: #ff5100;
  color: #fff;
  border: 2px solid ff5100 ;
  font-size: 15px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  transition: 0.2s ease;
  padding: 5px 0;
  text-align: center;
  cursor: pointer;
}
